@import url('https://fonts.googleapis.com/css?family=Roboto:300,400');



ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.container-fluid {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

ul a {
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 20px;
    padding-right: 20px;
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    font-weight: bold;
    color: #a7bcc1;
}


